<template>
<div id="guide">
  <div class="guidewrapper">
    <Row type="flex" align="middle" class-name="titleline">
      <Col span="4">
        <span class="helptitle">操作指引</span>
      </Col>
<!--       <Col class-name="more" offset="16" span="4">
        <span class="download">下载文档</span>
      </Col>
 -->    </Row>
    <div class="helpcard" v-if="card1">
      <div class="innertitle"  @click="add1">创建问卷</div>
      <div class="innerhr"></div>
      <div class="innercontent" @click="add1">
        <p>天外天问卷有三种创建问卷的方式供您选择使用：创建空白问卷、选择问卷模板、文本编辑问卷，可根据自身的使用习惯，选择最合适的方式来快速创建一份问卷。</p>
        <transition name="fade">
          <div v-if="showdetail1">
            <p class="contenttitle">一、创建空白问卷</p>
            <p>创建空白问卷，是指直接创建一份没有任何信息的空白问卷。点击“创建空白问卷”开始按钮，就可以创建一份空白问卷，设计和编辑问卷可同时进行，在左侧的“题目控件”添加题目，单选、多选、单行文本、多行文本、量表题等。问卷标题、欢迎语、问题标题、选项可根据自己的需求内容录入。</p>
            <p class="contenttitle">二、选择问卷模板</p>
            <p>使用模板创建问卷，通过“选择问卷模板”下的开始，根据自己需要找到相近的模板，预览后点击右下角“使用该模板创建问卷”，就直接创建一份带有模板问题的问卷。</p>
            <p class="contenttitle">三、文本编辑器</p>
            <p>如果您已经有设计好的WORD或TXT问卷，可以直接使用”文本编辑器“导入问卷文本直接生成在线问卷。使用复制粘贴的操作把WORD的问卷复制到文本编辑区，再修改题目格式。</p>
            <span class="rise" @click.stop="hidedetail1">收起<Icon class="riseicon" type="arrow-up-b"></Icon></span>
          </div>
        </transition>
      </div>
    </div>
    <div class="helpcard" v-if="card2">
      <div class="innertitle" @click="add2">设置问卷</div>
      <div class="innerhr"></div>
      <div class="innercontent"  @click="add2">
        <p>天外天问卷可以通过对问卷的显示、回收、协同编辑进行精细化的设置，根据自身的需要对相应的功能进行开关，实现智能、灵活的在线问卷管理。
          进入问卷详情页，上方菜单“设置”，如下图
        </p>
        <transition name="fade">
          <div v-if="showdetail2">
            <p class="contenttitle">1、显示设置</p>
            <p>打开”问卷显示问题编号“则在回答问卷时，显示每道题目对应的编号。
            </p>
            <p class="contenttitle">2、回收设置</p>
            <p>打开”设定回收结束时间“时，问卷会在设定的对应结束时间暂停回收，数据提交无法记录。
              打开“答题需要登录验证”、“每个用户只能回答一次”，则回答问卷需要登陆天外天账号，而且每个天外天账号只能回答一次问卷，不能重复提交，在统计分析结果中“导出EXCEL”可查看每份问卷填写者的天外天账号。
            </p>
            <p class="contenttitle">3、协同编辑</p>
            <p>为方便一份问卷的多人管理，天外天问卷实现协同编辑功能，打开此按钮可以添加天外天账号来对问卷编辑、统计进行协同管理，一份问卷可添加多个天外天账号作为协同管理员。
              添加一个协同者：正确输入协同者的天外天账号，点“添加人员”，即可完成添加。
              删除一个协同者：点协同者天外天账号旁边的“X”，即可删除此协同者。（问卷原始创建者不能被删除）
            </p>
            <span class="rise" @click.stop="hidedetail2">收起<Icon class="riseicon" type="arrow-up-b"></Icon></span>
          </div>
        </transition>
      </div>
    </div>
    <div class="helpcard" v-if="card3">
      <div class="innertitle" @click="add3">编辑问卷</div>
      <div class="innerhr"></div>
      <div class="innercontent" @click="add3">
        <p>腾讯问卷提供了简单、灵活的问卷编辑方式，可以便捷的完成问卷的标题、欢迎语、题目、题型、逻辑、结束语等的编辑，快速实现问卷的编写工作。</p>
        <transition name="fade">
          <div v-if="showdetail3">
            <p>点击问卷编辑页右上角“预览”按钮即可看到两种终端预览效果“电脑预览”和“手机预览”。问卷会根据不同的终端（电脑/手机）自适应。也可以在预览页通过“分享”按钮将问卷发送给其他人查阅。
              注意：预览状态下提交的问卷数据不会保存，请在问卷“开始回收”后提交。
            </p>
            <span class="rise" @click.stop="hidedetail3">收起<Icon class="riseicon" type="arrow-up-b"></Icon></span>
          </div>
        </transition>
      </div>
    </div>
    <div class="helpcard" v-if="card4">
      <div class="innertitle" @click="add4">预览问卷</div>
      <div class="innerhr"></div>
      <div class="innercontent" @click="add4">
        <p>天外天问卷有三种创建问卷的方式供您选择使用：创建空白问卷、选择问卷模板、文本编辑问卷，可根据自身的使用习惯，选择最合适的方式来快速创建一份问卷。</p>
        <transition name="fade">
          <div v-if="showdetail4">
            <p class="contenttitle">1、欢迎语和结束语编辑</p>
            <p>打开或新建一份问卷，进入问卷编辑界面，可自行修改系统提供的欢迎语和结束语，如下所示。</p>
            <p class="contenttitle">2、题目编辑</p>
            <p>点击页面左侧“题目控件”中的一个题型即可在问卷中新增一个对应的题目。在“题目”后输入问卷问题，在”备注“中输入对该问题的解释说明（如无需说明可不填）。
              题目下方的小图标依次是下移、上移、编辑、复制、删除、逻辑。点击相应功能就能完成编辑操作。
            </p>
            <p class="contenttitle">3、选项编辑</p>
            <p>点击“新建选项”添加选项，也可批量添加。添加“其他”选项可以在该选项之后填写文本内容。</p>
            <span class="rise" @click.stop="hidedetail4">收起<Icon class="riseicon" type="arrow-up-b"></Icon></span>
          </div>
        </transition>
      </div>
    </div>
<!--     <div class="rebackwrapper">
      <reback></reback>
    </div>
 -->  </div>
</div>
</template>

<script type="text/ecmascript-6">
import reback from '../reback/reback.vue'
export default {
  components: {
    'reback': reback
  },
  data () {
    return {
      showdetail1: false,
      showdetail2: false,
      showdetail3: false,
      showdetail4: false,
      card1: true,
      card2: true,
      card3: true,
      card4: true
    }
  },
  methods: {
    add1 () {
      this.showdetail1 = true
      this.card2 = false
      this.card3 = false
      this.card4 = false
    },
    hidedetail1 () {
      this.showdetail1 = false
      this.card2 = true
      this.card3 = true
      this.card4 = true
    },
    add2 () {
      this.showdetail2 = true
      this.card1 = false
      this.card3 = false
      this.card4 = false
    },
    hidedetail2 () {
      this.showdetail2 = false
      this.card1 = true
      this.card3 = true
      this.card4 = true
    },
    add3 () {
      this.showdetail3 = true
      this.card1 = false
      this.card2 = false
      this.card4 = false
    },
    hidedetail3 () {
      this.showdetail3 = false
      this.card1 = true
      this.card2 = true
      this.card4 = true
    },
    add4 () {
      this.showdetail4 = true
      this.card1 = false
      this.card2 = false
      this.card3 = false
    },
    hidedetail4 () {
      this.showdetail4 = false
      this.card1 = true
      this.card2 = true
      this.card3 = true
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.fade-enter-active, .fade-leave-active 
  transition opacity .5s

.fade-enter, .fade-leave-to
  opacity 0

#guide
  position relative
  top 130px
  background-color #f3f4f6
  height 100%
  overflow-y auto
.guidewrapper
  max-width 700px
  height 100%
  margin 0 auto
  margin-bottom 200px
  padding-top 22px
.helpcard
  background-color #fff
  min-height 120px
  margin-top 5px
  border-radius 4px
  padding 15px 25px
  position relative
.answercard
  height 250px
.helptitle
  color rgba(153, 153, 153, 0.84)
  font-size 19px
  height 22px
.more
  color #00a1e9
.more span 
  float right
  cursor pointer
.download
  margin-right 20px
.titleline
  margin-bottom 15px
.innertitle
  font-size 20px
  color #505050
  margin-left 2px
  cursor pointer
.innercontent
  margin 0 2px
  margin-top 12px
  font-size 13px
  color #505050
  line-height 22px
  cursor pointer
.contenttitle
  font-size 15px
  margin 5px 0
.innerhr
  border-top 1px solid #dadada
  margin-top 6px
.rise
  display block
  margin-top 20px
  margin-right 4px
  color #00a1e9
  text-align right
  cursor pointer
.riseicon
  margin-left 7px
.rebackwrapper
  margin-top 50px
</style>



